
*{
  margin: 0;
  padding: 0;
  font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","\5FAE\8F6F\96C5\9ED1",Arial,sans-serif;
  color: #606266;
  font-size: 14px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
@font-face {font-family: "iconfont";
  src: url(data:application/vnd.ms-fontobject;base64,); /* IE9 */
  src: url(data:application/vnd.ms-fontobject;base64,#iefix) format('embedded-opentype'), 
  url('data:application/x-font-woff2;charset=utf-8;base64,') format('woff2'),
  url(data:application/font-woff;base64,) format('woff'),
  url(data:application/x-font-ttf;base64,) format('truetype'), 
  url(data:image/svg+xml;base64,#iconfont) format('svg'); /* iOS 4.1- */
}
.iconfont {
  font-family: "iconfont" !important;
  font-size: 16px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.icon-down:before {
  content: "\E69B";
}
.icon-upward:before {
  content: "\E6A1";
}

.virus{
  width: 100%;
  margin: 0 auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding-bottom: 10px;
}
.virus #map{
  background-color: #f4f4f4;
}
.virus .info{
  width: 95%;
  max-width: 800px;
  margin: 10px auto;
  -webkit-box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
          box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
}
.virus .total{
  padding-bottom: 10px;
}
.virus .total header{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.virus .total header .title{
  font-size: 18px;
  color: #303133;
  padding: 10px;
}
.virus .total header .lasttime{
  font-size: 12px;
  color: #909399;
}
.virus .total .count{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.virus .total .count .hint{
  padding-left: 5px;
  text-align: end;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.virus .total .count .all{
  margin-bottom: 15px;
}
.virus .total .count .data{
  padding: 0 5px;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.virus .total .count .data .kind{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.virus .total .count .num{
  font-size: 18px;
  font-weight: 500;
  line-height: 1.4;
}
.virus .total .count :nth-child(1) .num{
  color: #F56C6C;
}
.virus .total .count :nth-child(2) .num{
  color: #E6A23C;
}
.virus .total .count :nth-child(3) .num{
  color: #909399;
}
.virus .total .count :nth-child(4) .num{
  color: #67C23A;
}
.virus .total .count .text{
  font-size: 16px;
  text-align: center;
}
.virus #line{
  padding: 5px;
}
.virus .detail{
  padding: 5px;
}
.virus .detail h2{
  font-size: 16px;
  color: #303133;
  font-size: 18px;
  padding: 5px;
}
.virus .detail header{
  font-size: 16px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.virus .detail header p,.virus .detail .item p{
  width: 25%;
  text-align: center;
  vertical-align: center;
}
.virus .detail header p:first-child,.virus .detail .item p:first-child{
  width: 20%;
}
.virus .detail .item{
  margin: 10px 0;
  font-size: 16px;
  width: 100%;
}
.virus .detail .item .province{
  background-color: rgba(192,196,204,0.3);
  padding: 5px;
  border-radius: 30px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  position: relative;
  -webkit-transition: all 1s ease-in-out;
  transition: all 1s ease-in-out;
}
.virus .detail .item .province i{
  position: absolute;
  right: 5px;
}
.virus .detail .item .all-city{
  max-height: 0;
  overflow: hidden;
  -webkit-transition: all .5s;
  transition: all .5s;
}
.virus .detail .item .show{
  max-height: 100vh;
}
.virus .detail .item .all-city .city{
  margin: 10px auto;
  padding-bottom: 5px;
  border-bottom: 1px solid #e4e7ed;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  white-space: nowrap;
}
.virus .detail .item .all-city .city:last-child{
  border: none;
}
